#interactive-game_el {
  position: relative;
  text-align: center;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;

  @include break-max(768px) {
    margin-top: 20px;
    padding-top: 20px;
    &::before {
      position: absolute;
      left: 10%;
      top: 0;
      height: 2px;
      width: 80%;
      background: $c0;
      content: '';
      border-radius: 3px; } }

  > h2, h3 {
    margin-top: 0;
    color: $c0;
    font-weight: normal; }

  h3 {
    font-size: rem(18px);
    font-family: default-font(300); }

  h2 {
    margin: 0 auto 10px;
    font-size: rem(22px);
    @include break-max(768px) {
      max-width: 80%; } }

  @include desktop {
    width: 37%;
    max-width: 420px;
    padding: 0 20px; } }

.oxford-logo {
  max-width: 180px;
  height: auto; }

.question {
  margin-left: auto;
  margin-right: auto;
  max-width: 330px;
  color: $c1;
  font-weight: normal;
  font-family: default-font(300); }

.answers {
  &.answered {
    .answer .values::after {
      animation: answereds 0.2s 0.5s $move forwards; } } }

.answer {
  max-width: 340px;
  margin: 0 auto;
  padding: 6px;
  border-radius: 50px;
  overflow: hidden;
  cursor: pointer;
  border: 1px solid rgba($c0, 0);
  transition: 0.4s $enter background;
  &:hover {
    border-color: rgba($c0, 0.2);
    background: rgba($c0, 0.2); }

  &:last-child {
    margin-bottom: 12px; }

  &.right .values::after {
    background: $c3; }
  &.wrong .values::after {
    background: $c2; }

  .values {
    position: relative;
    padding: 12px 20px;
    overflow: hidden;
    border-radius: 30px;

    &::after, &::before {
      position: absolute;
      left: 0;
      top: 0;
      z-index: 1;
      content: '';
      height: 100%;
      border-radius: 50px; }
    &::before {
      width: 100%;
      background: $c0; }
    &::after {
      background: $c4;
      width: var(--size);
      transform-origin: 0;
      transition: transform $enter 0.2s;
      transform: scale3d(0, 1, 1); } }

  span {
    color: $c5;
    font-size: rem(14px);
    font-family: default-font(500); }

  h6, span {
    position: relative;
    z-index: 2;
    text-shadow: none; }
  h6 {
    margin: 0;
    display: block;
    font-size: rem(18px);
    font-family: default-font(500);
    font-weight: normal;
    text-align: left;
    flex: 1; } }

.question-counter {
  position: relative;
  margin: 15px auto 18px;
  max-width: 180px;
  text-align: center;
  color: $c1;
  font-size: rem(16px);
  opacity: 0.5;
  h5 {
    font-weight: normal;
    margin: 0 10px;
    text-shadow: none; }

  &::after, &::before {
    content: '';
    height: 1px;
    flex: 1;
    background: rgba($c1, 0.8); } }

.oxford-explains-btn {
  position: relative;
  margin-top: 12px;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  max-width: 320px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px 30px;
  border: 0;
  border-radius: 50px;
  background: $c6;
  color: $c0;
  font-size: rem(16px);
  font-family: default-font(700);
  text-decoration: none;
  overflow: hidden;
  transition: transform $move 0.2s;
  will-change: transform;
  span {
    position: relative; }
  &:hover {
    transform: scale3d(1.02, 1.02, 1); }
  .progress {
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    border-radius: 50px;
    background: $c8;
    transform-origin: 0;
    transform: scale3d(0, 1, 1); }

  #icon-lupe {
    position: relative;
    margin-right: 5px;
    fill: $c0;
    height: 28px;
    width: 28px;
    pointer-events: none; } }

@keyframes answereds {
  0% {
    transform: scale3d(0, 1, 1); }
  100% {
    transform: scale3d(1, 1, 1); } }
